<template>
  <text y="20" :x="x" :style="{fill: color, stroke: color}">{{score.toFixed(2)}}</text>
</template>

<script>
import store from "../store";
export default {
  name: "MultiScore",
  store,
  props: {
    id: {
      type: Number,
      default: 1
    },
    room: {
      type: Number,
      default: 0
    },
    x: {
      type: Number,
      default: 380
    },
    currentPlayer: {
      type: Number,
      default: 0
    },
    color: {
      type: String,
      default: "#ffffff"
    }
  },
  data() {
    return {
      score: 0
    };
  },
  socket: {
    events: {
      score(data) {
        if (data.id === this.id) this.score += data.score;
      }
    }
  },
  watch: {
    "$store.state.score"(deltaScore) {
      if (this.currentPlayer === this.id) {
        if (deltaScore > 0) {
          this.score += deltaScore
          this.$io.socket.post(`/multi/${this.room}/score`, {
            id: this.id,
            score: deltaScore
          });
        }
      }
    }
  }
};
</script>

